.control {
  margin: 0 auto;
  float: none;
  font-family: monospace;
}

.control .control-applicability {
  text-align: right;
  vertical-align: middle;
}

.control .control-guideline,.control-topic {
  text-decoration: underline;
  text-transform: uppercase;
}

.control .control-applicability,.control-description,.control-identifier {
  font-family: monospace;
}

.control .control-description {
  margin-top: 2em;
}

.control p {
  white-space: pre-wrap;
}

.ISMControl-logo {
  height: 40vmin;
  pointer-events: none;
}

@media screen and (max-width: 800px) {
  .control-checksum {
    display: none;
  }
  .control-checksum-spacer {
    display: block;
  }
}

@media screen and (min-width: 800px) {
  .control-checksum {
    display: none;
  }
  .control-checksum-spacer {
    display: block;
  }
}

.ISMControl-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.ISMControl-link {
  color: #61dafb;
}

.nochecksum {
  display: none;
}